import React from "react"
import Grid from "@mui/material/Grid"
import "./Card.scss"
import { substring } from "../../utils/helpers"
import { Link } from "gatsby"
function Card({ cardData, col, gap }) {
  return (
    <div className="blog-card">
      <Grid container spacing={gap} justifyContent="center">
        {cardData.map((card, i) => {
          return (
            <Grid item md={col} key={i}>
              <Link to={`/blogs/${card.id}`} style={{ textDecoration: "none" }}>
                <div className="img-wrapper text-center">
                  <img src={card.img} alt="img" className="" />
                </div>
                <p className="date-card">{card.date}</p>
                <p className="header-card">{card.header}</p>
                <p className="text-card">{substring(card.text, 170)}</p>
              </Link>
            </Grid>
          )
        })}
      </Grid>
    </div>
  )
}

export default Card
